						<div class="swiperBox">
							<div class="picture">
								<img src="../assets/achieve.png" alt="" class="achieve" v-if="(points >= list.score)">
								<div class="picImg">
									<img :src="list.src" alt="">
								</div>
								<div class="white">
									<div class="giftName">{{list.name}}</div>
									<div class="integral">{{list.integral}}积分</div>
								</div>
