				</swiper>
				<div class="box">
					<div>1丶通过邮件发放，玩家使用后调出抽奖转盘，转盘含北溟鲲</div>
					<div>2丶可以获得永久冥醌，空间挂件（30天），金柳露，月华露，银币，红罗羹，绿芦羹，星辰碎片等道具。</div>
