				<div class="thanks">
					感谢以下的朋友为发起者
					<span class="red">{{role_name}}</span>
					的梦幻秀助力
				</div>
				<div class="clearFix personInfo" v-for="list in friendArr" :key="list.time">
					<img :src="list.user_image" alt="" class="lt">
					<div class="lt">
						<div class="strong">{{list.user_name ? list.user_name : ''}}</div>
						<div>{{list.message ? list.message : ''}}</div>
					</div>
					<div class="rl code">
						+ {{list.points}}
					</div>
				</div>
