<script>
import 'swiper/dist/css/swiper.css'
import axios from 'axios'
import { Dialog } from 'vant';
import { swiper, swiperSlide } from 'vue-awesome-swiper'
import sucessPage  from './sucessPage'
import congratulate from './congratulate'
import takePart from './takePart'
import rule from './rule'
import head from '../assets/head.png'
import person from '../assets/person.png'
import pic50 from '../assets/integral1.png'
import pic100 from '../assets/integral2.png'
import pic200 from '../assets/integral3.png'
import pic300 from '../assets/integral4.png'

export default {
	name: 'HelloWorld',
	components: {
    swiper,
		swiperSlide,
		sucessPage,
		congratulate,
		takePart,
		rule
	},
	watch: {
		
	},
  data () {
    return {
			isOutSide: '',//判断是否外部界面
			windowOpen: false,
			windowName: '',
			pictureSrc: 'http://yxzs.163.com/appresource/g18/',
			swiperCard:[
				{
					id: 1,
					name: '随机羊角包头饰',
					integral: '50',
					src: pic50,
					score: 50
				}, {
					id: 2,
					name: '50000银币',
					integral: '100',
					src: pic100,
					score: 100
				}, {
					id: 3,
					name: '翠羽金屏头像框',
					integral: '200',
					src: pic200,
					score: 200
				}, {
					id: 4,
					name: '北溟鲲转盘',
					integral: '300',
					src: pic300,
					score: 300
				}, {
					id: 5,
					name: '北溟鲲转盘',
					integral: '400',
					src: pic300,
					score: 400
				}, {
					id: 6,
					name: '北溟鲲转盘',
					integral: '500',
					src: pic300,
					score: 500
				}
			],
			swiperOption: {
				// some swiper options/callbacks
				// 所有的参数同 swiper 官方 api 参数
				// ...
				// autoplay:true,
				loop: true, // 循环模式选项
				slidesPerView : 3,
				// slidesPerview:'auto',
				centeredSlides : true,
				on: {
					click: (e) => {
						let swiper = this.$refs.mySwiper.swiper;
						let integral =swiper.clickedSlide.getElementsByClassName('integral')[0].innerHTML;
						if(integral === '50积分') {
							this.congratulateParise = this.swiperCard[0]
						} else if(integral === '100积分') {
							this.congratulateParise = this.swiperCard[1]
						} else if(integral === '200积分') {
							this.congratulateParise = this.swiperCard[2]
						} else if(integral  === '300积分') {
							this.congratulateParise = this.swiperCard[3]
						} else if(integral  === '400积分') {
							this.congratulateParise = this.swiperCard[4]
						} else if(integral  === '500积分') {
							this.congratulateParise = this.swiperCard[5]
						}
						this.windowOpen = true;
						this.windowName = 'congratulate'
					},
				}
			},
			swiperOption2: {
				direction : "vertical",
				autoplay: true,
				loop: true,
				centeredSlides: true,
			},
			role_id: '',
			token: '',
			token_type: '',
			roleData: null, //角色数据
			show_img: '',
			user_image: '',
			role_name: '',
			friendArr:[],
			broadcast: [],//轮播图
			// broadcastPic: '', //轮播图头像
			points: '',//分数
			helpPoints: '',//助力的分数
			highPointsAchieve: '', //最高达成的奖励
			restPoint: '', //剩余分数
			congratulateParise: {},
			shareParam: '',
		// 初始化分享组件
			mobileShare: ''
    }
	},
	computed: {
		swiper() {
			return this.$refs.mySwiper.swiper
		},
		swiper2() {
			return this.$refs.mySwiper2.swiper
		}
	},
	created() {
		this.role_id = this.getParameter('role_id');
		this.shareParam = {
			title: '我正在参加《梦幻西游》手游捏脸活动，为我助力吧！',
			desc: '我捏的脸这么好看，快来给我点赞帮我抱走永久北溟鲲啦～你也一起来玩吧！',
			link: `https://exth5.dev.16163.com/?role_id=${this.role_id}&utm_source=ds`,
			imgUrl:'https://webinput.nie.netease.com/img/my/icon.png',
			socialNetwork: 0
		}
		this.mobileShare = new MobileShare({
			title: '我正在参加《梦幻西游》手游捏脸活动，为我助力吧！',
			desc: '我捏的脸这么好看，快来给我点赞帮我抱走永久北溟鲲啦～你也一起来玩吧！',
			link: `https://exth5.dev.16163.com/?role_id=${this.role_id}&utm_source=wx`,
			imgUrl:'https://webinput.nie.netease.com/img/my/icon.png',
		})
		this.init();
	},
	mounted() {
		//初始化分享组件
		let meta = document.getElementsByTagName('meta');
    meta[1].content = "user-scalable=0"
		// current swiper instance
		// 然后你就可以使用当前上下文内的swiper对象去做你想做的事了
		this.swiper.slideTo(3, 1000, false);
		// var mo=function(e){e.preventDefault()};
		// document.body.style.overflow='hidden';
		// document.addEventListener("touchmove",mo,false);//禁止页面滑动
	},
	methods: {
		shareTo(){
			if (ds.isGodlike) {
				ds.doShare(this.shareParam)
			} else {
				this.mobileShare.show()
			}
		},
		getParameter: function (name) {
      var query = window.location.search;
      var iLen = name.length;
      var iStart = query.indexOf(name);
      if (iStart == -1)
          return "";
      iStart += iLen + 1;
      var iEnd = query.indexOf("&", iStart);
      if (iEnd == -1)
          return query.substring(iStart);
      return query.substring(iStart, iEnd);
    },
		init() {
			let _this = this;
			//存储关键参数
			this.token = this.$route.query.code;
			this.role_id = this.$route.query.role_id;
			this.token_type = this.$route.query.token_type;
			console.log('role_id:' + this.role_id)
			console.log('token:' + this.token)
			console.log('token_type:' + this.token_type)

			//无role_id且不在大神内引导
			if( !this.role_id && this.token_type !== 'ds' ) {
				this.windowOpen = true;
				this.windowName = 'takePart'
				this.isOutSide = true;	
			}
			
			let url = `/mhshow_h5/get_roleinfo?role_id=${this.role_id}&token=${this.token}&token_type=${this.token_type}`;
			axios.get(url).then((res) => {
				if(res && res.data && res.data.result) {
					_this.roleData = res.data.result
					_this.role_name = _this.roleData.role_name ? _this.roleData.role_name : '';
					_this.user_image = _this.roleData.icon ? `${this.pictureSrc + _this.roleData.icon}.png` : head;
					_this.points = _this.roleData.points;
					_this.getFriendList();
					if(	_this.points < 50) {
						_this.highPointsAchieve = '随机羊角包头饰',
						_this.restPoint = 50 - _this.points
					} else if(_this.points >= 50 && _this.points < 100) {
						_this.highPointsAchieve = '50000银元',
						_this.restPoint = 100 - _this.points
					} else if(_this.points >= 100 && _this.points < 200) {
						_this.highPointsAchieve = '翠羽金屏头像框',
						_this.restPoint = 200 - _this.points
					} else if(_this.points >= 200 && _this.points < 300) {
						_this.highPointsAchieve = '北溟鲲转盘'
						_this.restPoint = 300 - _this.points
					} else if(_this.points >= 300 && _this.points < 400) {
						_this.highPointsAchieve = '北溟鲲转盘'
						_this.restPoint = 400 - _this.points
					} else if(_this.points >= 400 && _this.points < 500) {
						_this.highPointsAchieve = '北溟鲲转盘'
						_this.restPoint = 500 - _this.points
					}
					_this.show_img = _this.roleData.show_img ? _this.roleData.show_img : person;
				}
			}).catch(error => {
				console.log(error, 'errorerrorerrorerror')
			})
		},
		getFriendList() {
			let _this = this;
			let url = `/mhshow_h5/get_role_assistances?role_id=${this.role_id}&user_num=5`;
			axios.get(url).then((res) => {
				if(res && res.data && res.data.result) {
					let result = res.data.result
					for(let i = 0; i < result.length; i++) {
						if(result[i].points === 10)	{
							result[i].message = '缘分深厚，积分狂增!'	
						}else if(result[i].points === 8) {
							result[i].message = '缘分上佳，积分大增!'	
						}else {
							result[i].message = '运气不错，积分增加!'	
						}
					}
					_this.friendArr = result;
				}
				if(res && res.data && res.data.broadcast) {
					let broadcast = res.data.broadcast
					for(let i = 0; i < broadcast.length; i++) {
						broadcast[i].picSrc = `${this.pictureSrc + broadcast[i].icon}.png`
					}
					_this.broadcast = broadcast;
				}
			})	
		},
		helpDay() {
			let _this = this;
			let formData = new FormData();
			formData.append("role_id", this.role_id);
			let url = `/mhshow_h5/assistance`
			axios.post(url,formData).then((res) => {
				if(res && res.data) {
					_this.helpPoints = res.data.points;
					_this.windowOpen = true;
					_this.windowName = 'sucessPage'
				}
			}).catch(err => {
				_this.helpPoints = 5
				_this.windowOpen = true;
				_this.windowName = 'sucessPage'
				alert('今天已点赞啦，客官明日再来吧')
			})
		},
		callback() {
		},
		clickPraise() {
			this.helpDay()
		},
		gain() {
			this.windowOpen = true;
			this.windowName = 'takePart'	
		},
		ruleExplain() {
			this.windowOpen = true;
			this.windowName = 'rule'
		},
		getHelp(val) {
			if(val) {
				this.init()
			}
		}
	}
}
</script>

