				.swiperBig2 {
					height: 0.09rem;
					margin-left: 0 !important;
				}
				.swiperBox{
					.picture {
						position: relative;
						width: 70%;
						.achieve {
							position: absolute;
							top: 0px;
							right: -20px;
							z-index: 99;
						}
						.giftName {
							padding: 10px 0;
						}
						.integral {
							font-size: 20px
						}
					}
					.picImg{
						position: relative;
						background-color: #f8ebc8;
						border-top-left-radius: 15px;
						border-top-right-radius: 15px;
						> div {
							position: absolute;
							width: 100%;
							bottom: 0px;
							font-weight: 700px;
							padding: 8px 0;
							background: rgba(0, 0, 0, 0.5)
						}
					}
					img {
						// background-color: #f8ebc8;
						padding: 0 20px 
					}
					.picture {
						padding: 5px;
						border-radius: 15px;
						// background-color: #f8ebc8;
						display: inline-block;
						// width: .2rem;
						border: 3px solid #f8ebc8;
					}
				}
				
			}
	}
</style>



